import axios from "axios";
import moment from "moment/moment";
import React, { useEffect, useState } from "react";
import Navigation from "../components/Navigation";
import Title from "../components/Title";
import Card from "../components/Card";
import Caroussel from "../components/Caroussel";

const ThisWeek = ({ genreMap }) => {
  const [movies, setMovies] = useState([]);
  const [storedCard, setStoredCard] = useState([]);

  useEffect(() => {
    const fetchedMoviesThisWeek = () => {
      // Calcule  de la date de début et de fin de la semaine en cours
      const debutSemaine = moment().startOf("week").format("YYYY-MM-DD"); //lundi
      const finSemaine = moment().endOf("week").format("YYYY-MM-DD"); // dimanche

      axios
        .get(
          `https://api.themoviedb.org/3/discover/movie?api_key=1c8ab441c3e6bb7d34570ca225b9cbbe&primary_release_date.gte=${debutSemaine}&primary_release_date.lte=${finSemaine}&language=fr`
        )
        .then((res) => setMovies(res.data.results));
    };
    fetchedMoviesThisWeek();
    // console.log(movies);
  }, []);

  const handleAddToFavs = (movies) => {
    const alreadyStoredFilmsInLS =
      JSON.parse(localStorage.getItem("films")) || []; // récupère les films déjà ds le LS , si y'a rien c'est [].
    if (!alreadyStoredFilmsInLS.find((fav) => fav.id === movies.id)) {
      // ci dessus: Cette méthode vérifie si le film est déjà présent dans la liste.
      const updatedStoredCard = [...alreadyStoredFilmsInLS, movies];
      //mise à jour de l'état locale ci dessous:
      setStoredCard(updatedStoredCard);
      localStorage.setItem("films", JSON.stringify(updatedStoredCard));
      //C'est updatedStoredCard qu'on envoie dans le local storage et non storedCard directement car quand on setStoredCard la state de storedCard la mise à jour est pas immédiate
    }
  };

  return (
    <div className="home-container">
      <header>
        <Navigation />
        <Title />
      </header>
      <div className="videos-container">
        <h2 className="bandeAnnonce">
          {" "}
          <span id="jaune"></span>Bandes-Annonces à ne pas manquer :
        </h2>

        <Caroussel />
      </div>
      <h2 className="sous-titre">Films à l'affiche cette semaine : </h2>
      <ul id="results">
        {movies.slice(0, 4).map((film) => (
          <div key={film.id}>
            <Card
              movies={film}
              genreMap={genreMap}
              addToLocalStorage={handleAddToFavs}
            />
          </div>
        ))}
      </ul>
    </div>
  );
};

export default ThisWeek;

//Cette page permet d'obtenir et d'afficher tous les films à l'affiche chaque semaine uniquement
