import React, { useEffect, useState } from "react";
import Navigation from "./../components/Navigation";
import Title from "../components/Title";
import axios from "axios";
import Card from "../components/Card";

const Accueil = () => {
  const [fetchedData, setFetchedData] = useState([]);
  const [inputSearch, setInputSearch] = useState("code"); // useState initialisé avec requête sur deadpool quand on arrive sur le site
  const [storedCard, setStoredCard] = useState([]);
  const [filter, setFilter] = useState("popular");
  const [genreList, setGenreList] = useState([]);

  // Charger la liste des genres au chargement de la page
  useEffect(() => {
    axios
      .get(
        "https://api.themoviedb.org/3/genre/movie/list?api_key=1c8ab441c3e6bb7d34570ca225b9cbbe&language=fr"
      )
      .then((res) => setGenreList(res.data.genres));
  }, []);

  useEffect(() => {
    const searchMovie = () => {
      // Vérifier si l'entrée correspond à un genre
      const genre = genreList.find(
        (g) => g.name.toLowerCase() === inputSearch.toLowerCase()
      );

      let url = "";

      if (genre) {
        // Si un genre correspondant est trouvé, effectuer une recherche par genre
        url = `https://api.themoviedb.org/3/discover/movie?api_key=1c8ab441c3e6bb7d34570ca225b9cbbe&with_genres=${genre.id}&language=fr`;
      } else {
        // Sinon, effectuer une recherche par titre de film
        url = `https://api.themoviedb.org/3/search/movie?api_key=1c8ab441c3e6bb7d34570ca225b9cbbe&query=${inputSearch}&language=fr`;
      }

      if (url) {
        axios.get(url).then((res) => setFetchedData(res.data.results));
      }
    };

    if (inputSearch) {
      searchMovie();
    } //la condition if (inputSearch) est une mesure de sécurité pour éviter les requêtes inutiles.
  }, [inputSearch, genreList]);

  const genreMap = {
    28: "Action",
    12: "Adventure",
    16: "Animation",
    35: "Comedy",
    80: "Crime",
    99: "Documentary",
    18: "Drama",
    10751: "Family",
    14: "Fantasy",
    36: "History",
    27: "Horror",
    10402: "Music",
    9648: "Mystery",
    10749: "Romance",
    878: "Science Fiction",
    10770: "TV Movie",
    53: "Thriller",
    10752: "War",
    37: "Western",
    10759: "Action & Adventure",
    10762: "Kids",
    10763: "News",
    10764: "Reality",
    10765: "Sci-Fi & Fantasy",
    10766: "Soap",
    10767: "Talk",
    10768: "War & Politics",
  };

  //Fonction qui gère le tri
  function handleTri(type) {
    let sortedFetch = [...fetchedData]; //Creer une copie du [] fetchedData
    if (type === "top") {
      sortedFetch.sort((a, b) => b.vote_average - a.vote_average);
    } else if (type === "flop") {
      sortedFetch.sort((a, b) => a.vote_average - b.vote_average);
    }
    setFetchedData(sortedFetch); // assigne la copie triée de fetchedData à fetchedData
  }

  const handleAddToFavs = (movies) => {
    const alreadyStoredFilmsInLS =
      JSON.parse(localStorage.getItem("films")) || []; // récupère les films déjà ds le LS , si y'a rien c'est [].
    if (!alreadyStoredFilmsInLS.find((fav) => fav.id === movies.id)) {
      // ci dessus: Cette méthode vérifie si le film est déjà présent dans la liste.
      const updatedStoredCard = [...alreadyStoredFilmsInLS, movies];
      //mise à jour de l'état locale ci dessous:
      setStoredCard(updatedStoredCard);
      localStorage.setItem("films", JSON.stringify(updatedStoredCard));
      //C'est updatedStoredCard qu'on envoie dans le local storage et non storedCard directement car quand on setStoredCard la state de storedCard la mise à jour est pas immédiate
    }
  };

  return (
    <div className="home-container">
      <header>
        <Navigation />
        <Title />
      </header>
      <div className="search-container">
        <input
          type="text"
          placeholder="Rechercher par nom ou par genre.."
          onChange={(e) => {
            setInputSearch(e.target.value); //Mettre à jour le state pour re-render l'input à chaque changement
            setFilter("search"); // Mettre à jour le filtre pour "search" lorsque l'utilisateur tape quelque chose
          }}
        />
        {/* <button>Rechercher</button> Après je peux rajouter si je retravaille le code */}
        <div className="sort-container">
          <button id="top" onClick={() => handleTri("top")}>
            Top <span>➜</span>
          </button>
          <button id="flop" onClick={() => handleTri("flop")}>
            Flop <span>➜</span>
          </button>
          <p>Ex. : Matrix, Animation, Romance, Science fiction...</p>
        </div>
      </div>
      <ul id="results">
        {fetchedData.map((film) => (
          <div key={film.id}>
            <Card
              movies={film}
              genreMap={genreMap}
              addToLocalStorage={handleAddToFavs}
            />
          </div>
        ))}
      </ul>
    </div>
  );
};

export default Accueil;
