import React, { useState } from "react";
import moment from "moment/moment";
import "moment/locale/fr";

const Card = ({ movies, genreMap, addToLocalStorage }) => {
  const [buttonText, setButtonText] = useState("ajouter coup de coeur");
  const handleclickAndAddToStorage = () => {
    addToLocalStorage(movies); // ajouter film au coups de coeurs
    setButtonText("Coup de coeur ajouté !"); // modifiera au click le texte dans le bouton
  };

  const dateToFrench = (date) => {
    return moment(date, "YYYY MM DD").locale("fr").format("Do MMMM YYYY"); // pas besoin de caler ça ds une variable
  };

  return (
    <div className="movie-container">
      {movies.poster_path ? (
        <img
          src={"https://image.tmdb.org/t/p/w500" + movies.poster_path}
          alt={"poster :" + movies.title}
        />
      ) : (
        <img src="/assets/img/poster.jpg" alt={"poster :" + movies.title} />
      )}
      <h2>{movies.title}</h2>
      <p id="date">Sortie le : {dateToFrench(movies.release_date)}</p>
      <div className="note-container">
        <h4 id="note">{movies.vote_average.toFixed(1)} /10</h4>
        <h5>⭐</h5>
      </div>
      <ul>
        {movies.genre_ids.map((id) => (
          <span key={id} className="genres">
            {genreMap[id]}
          </span>
        ))}
      </ul>
      <p id="overview">{movies.overview}</p>
      <button id="cdc" onClick={() => handleclickAndAddToStorage(movies)}>
        {buttonText}
      </button>
      {/* stockage */}
    </div>
  );
};

export default Card;

//tout comme array[i], genreMap[id] permet d'accèder dynimiquement au valeur des id définit dans genreMap
//ici l'id est mappé du tableau genre_ids       "genre_ids": [28,12,35]

//En JavaScript, les crochets [] sont utilisés pour accéder à une propriété d'un objet de manière dynamique.
//toFixed(nombre de décimaux après la virgule) = permet d'arrondir un chiffre à la décimal choisi
//ex= 7,698.toFixed(1) = 7,7 (/!\ retourne un string pas un number)

// {genreMap[id]} = rentrer dans l'objet genreMap à id demandé (qui est une propriété de l'objet) et affichera la valeur de cette propriété dc le genre
