import { Splide, SplideSlide } from "@splidejs/react-splide";
import React, { useEffect, useState } from "react";
import "@splidejs/splide/dist/css/themes/splide-skyblue.min.css";
import axios from "axios";

const Caroussel = () => {
  const [upcomingMovies, setUpcomingMovies] = useState([]); // tableau de films à venir
  const [videos, setVideos] = useState([]);

  // Charge les films à venir afin d'utiliser leur id pour rechercher les videos trailer associées dans un autre endpoint
  useEffect(() => {
    const filmsAVenir = async () => {
      const res = await axios.get(
        `https://api.themoviedb.org/3/movie/upcoming?api_key=1c8ab441c3e6bb7d34570ca225b9cbbe&language=fr-FR&page=1`
      );
      setUpcomingMovies(res.data.results);

      // Ensuite, charge les vidéos
      const videosData = [];
      for (let i = 0; i < res.data.results.length; i++) {
        const response = await axios.get(
          `https://api.themoviedb.org/3/movie/${res.data.results[i].id}/videos?api_key=1c8ab441c3e6bb7d34570ca225b9cbbe&language=fr-FR`
        );
        if (response.data.results[0]) {
          videosData.push(response.data.results[0]);
        }
      }
      setVideos(videosData);
    };
    filmsAVenir();
  }, []);

  if (videos.length === 0) {
    return <p>Loading videos...</p>;
  }

  return (
    <Splide
      options={{
        type: "loop",
        perPage: 3, // Corrigé la casse ici
        autoplay: true,
        gap: "1rem",
        pagination: false,
        arrows: true,
        breakpoints: {
          768: {
            perPage: 1, // Sur mobile (moins de 768px), affiche 1 vidéo par page
          },
        },
      }}
    >
      {videos.slice(0, 6).map((video, index) => (
        <SplideSlide key={index} style={{ padding: "0 10px" }}>
          <iframe
            className="video-iframe"
            src={`https://www.youtube.com/embed/${video.key}`}
            title={video.name}
            allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
            allowFullScreen
          ></iframe>
        </SplideSlide>
      ))}
    </Splide>
  );
};

export default Caroussel;
