import React, { useState } from "react";
import Navigation from "../components/Navigation";
import Title from "../components/Title";

const CoupsDeCoeur = ({ genreMap }) => {
  const [moviesArrayState, setMoviesArrayState] = useState(() => {
    const movies = localStorage.getItem("films"); //initialise l'affichage des CDC avec ce qu'il y a dans le LS
    return movies ? JSON.parse(movies) : [];
  });

  //   console.log(moviesArray);, contient bien tableau des films stocké ds le LS

  const handleDeleteFromLS = (movieId) => {
    // 1, obtenir ce qui est ds le LS
    const storedFilm = JSON.parse(localStorage.getItem("films")) || [];
    // 2,filtrer la liste pour exclure le film avec l'id spécifier. Tous les films avec id différent de movieID, donc de celui que je passerai en paramètre de la fonction : on les garde. Donc movieID est exclu.
    const newArrayFiltered = storedFilm.filter((film) => film.id !== movieId);
    //Mettre à jour le LS avec la nouvelle liste de films.
    localStorage.setItem("films", JSON.stringify(newArrayFiltered));
    // Mettre à jour le rendu de moviesArrayState pour raffraichir l'affichage quand un film n'est plus dans le LS
    setMoviesArrayState(newArrayFiltered);
  };

  return (
    <div className="home-container">
      <header>
        <Navigation />
        <Title />
      </header>
      <h2 className="sous-titre">Coups de coeur</h2>

      <ul id="results">
        {moviesArrayState.length > 0 ? (
          moviesArrayState.map((film) => (
            <div key={film.id} className="movie-container">
              {film.poster_path ? (
                <img
                  src={"https://image.tmdb.org/t/p/w500" + film.poster_path}
                  alt="image film"
                />
              ) : (
                <img src="/assets/img/poster.jpg" alt="img film" />
              )}
              <h2>{film.title}</h2>
              <p id="date">Sortie le : {film.release_date}</p>
              <div className="note-container">
                <h4>{film.vote_average.toFixed(1)} /10</h4>
                <h5>⭐</h5>
              </div>
              <ul>
                {film.genre_ids &&
                  film.genre_ids.map((id) =>
                    genreMap[id] ? <span key={id}>{genreMap[id]}</span> : null
                  )}
              </ul>
              <p id="overview">{film.overview}</p>
              {/* <button onClick={handleStorage}>ajouter coup de coeur</button> */}
              <button id="cdc" onClick={() => handleDeleteFromLS(film.id)}>
                Supprimer de la liste
              </button>
            </div>
          ))
        ) : (
          <p>Vous n'avez pas encore de coups de coeur 😔 </p>
        )}
      </ul>
    </div>
  );
};

export default CoupsDeCoeur;

